<template>
  <div class="sys-guide">
    <span class="brand">cleverMock</span>
    <div class="title-panel">
      <div class="title">初始化设置</div>
    </div>
    <div class="form">
      <form-item label="管理员名称" :label-width="90">
        <c-input size="big" v-model="form.rootName" :width="300" autoFocus/>
      </form-item>
      <form-item label="管理员密码" :label-width="90">
        <c-input size="big" v-model="form.rootPassword" :width="300"/>
      </form-item>
      <c-button theme="border" type="primary" size="large" @click="onSave">保存</c-button>
    </div>
  </div>
</template>

<script>
import FormItem from '../../components/formItem/index'
import {actions} from '../../store/constants'
import {router} from '../../router/constants'
export default {
  name: 'sysGuide',
  components: {FormItem},
  data () {
    return {
      form: {
        rootName: 'admin',
        rootPassword: ''
      }
    }
  },
  methods: {
    onSave () {
      // todo 表单校验
      this.$store.dispatch(actions.sys.init, this.form).then(() => {
        this.$router.replace({
          name: router.login
        })
      }).catch(err => {
        this.$message({
          type: 'error',
          message: '系统初始化失败'
        })
        console.error('系统初始化失败', err)
      })
    }
  }
}
</script>

<style lang="sass">
@import "styles/index"
</style>
